.wp-block-gutenberg-custom-block-link.active {
  background-color: #ccc3;
  border-radius: 10px;
  padding: 10px; }
  .wp-block-gutenberg-custom-block-link.active .link.active {
    font-weight: bold; }
  .wp-block-gutenberg-custom-block-link.active .link [contenteditable] {
    min-width: 145px;
    display: inline-block; }

.wp-block-gutenberg-custom-block-link .link .icon {
  width: 17px;
  height: 17px;
  margin-right: 0.5rem; }

.wp-block-gutenberg-custom-block-link .item__add {
  width: 100%;
  text-align: center; }

.wp-block-gutenberg-custom-block-link .item__remove .icon {
  cursor: pointer;
  width: 17px;
  height: 17px;
  margin-left: 1rem; }

.block-title {
  color: #8aa4b8;
  width: 100%;
  text-align: center; }
